import React, { useEffect, useState, useMemo } from 'react';
import { motion } from 'framer-motion';
import { Button } from './ui/button';
import { ChevronDown, Github, Linkedin, Mail, FileText, Code2 } from 'lucide-react';

// Roles from CV experience - defined outside component to prevent re-creation
const ROLES = [
    'Operations & IT Manager',
    'Web Content & E-commerce Specialist',
    'Webmaster',
];

const HeroSection = () => {
    const [typedText, setTypedText] = useState('');
    const [roleIndex, setRoleIndex] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);

    useEffect(() => {
        const currentRole = ROLES[roleIndex];
        let timeout;
        
        if (!isDeleting) {
            // Typing
            if (typedText.length < currentRole.length) {
                timeout = setTimeout(() => {
                    setTypedText(currentRole.slice(0, typedText.length + 1));
                }, 80);
            } else {
                // Finished typing, wait then start deleting
                timeout = setTimeout(() => {
                    setIsDeleting(true);
                }, 2500); // Wait 2.5 seconds before deleting
            }
        } else {
            // Deleting
            if (typedText.length > 0) {
                timeout = setTimeout(() => {
                    setTypedText(typedText.slice(0, -1));
                }, 40); // Delete faster than typing
            } else {
                // Finished deleting, move to next role
                setIsDeleting(false);
                setRoleIndex((prev) => (prev + 1) % ROLES.length);
            }
        }
        
        return () => clearTimeout(timeout);
    }, [typedText, isDeleting, roleIndex]);

    const scrollToAbout = () => {
        const element = document.querySelector('#about');
        if (element) {
            element.scrollIntoView({ behavior: 'smooth' });
        }
    };

    const socialLinks = [
        { icon: Github, href: 'https://github.com/redzrex', label: 'GitHub' },
        { icon: Linkedin, href: 'https://linkedin.com/in/redzrex', label: 'LinkedIn' },
        { icon: Mail, href: 'mailto:redzaruslan@gmail.com', label: 'Email' },
    ];

    return (
        <section id="home" className="relative min-h-screen flex items-center justify-center overflow-hidden">
            {/* Background Grid Pattern */}
            <div className="absolute inset-0 bg-[linear-gradient(hsl(var(--primary)/0.03)_1px,transparent_1px),linear-gradient(90deg,hsl(var(--primary)/0.03)_1px,transparent_1px)] bg-[size:60px_60px]" />
            
            {/* Gradient Orbs */}
            <div className="absolute top-1/4 -left-1/4 w-96 h-96 bg-primary/10 rounded-full blur-[120px] animate-float" />
            <div className="absolute bottom-1/4 -right-1/4 w-96 h-96 bg-accent/10 rounded-full blur-[120px] animate-float animation-delay-500" />

            <div className="relative z-10 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20 lg:py-32">
                <div className="text-center">
                    {/* Terminal-style intro */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.6 }}
                        className="inline-flex items-center gap-2 px-4 py-2 rounded-full border border-primary/30 bg-primary/5 mb-8"
                    >
                        <span className="w-2 h-2 rounded-full bg-primary animate-pulse" />
                        <span className="font-mono text-sm text-primary">System Status: Online</span>
                    </motion.div>

                    {/* Name */}
                    <motion.h1
                        initial={{ opacity: 0, y: 30 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.6, delay: 0.2 }}
                        className="text-4xl sm:text-5xl lg:text-7xl font-bold tracking-tight mb-4"
                    >
                        <span className="text-foreground">Muhammad</span>
                        <br />
                        <span className="gradient-text">Redza Ruslan</span>
                    </motion.h1>

                    {/* Title with typing effect - Fixed height to prevent layout shift */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.6, delay: 0.4 }}
                        className="flex items-center justify-center gap-2 mb-6 h-8 sm:h-10"
                    >
                        <Code2 className="w-5 h-5 text-primary flex-shrink-0" />
                        <p className="font-mono text-lg sm:text-xl text-muted-foreground whitespace-nowrap">
                            <span className="text-primary">const</span> role = &quot;
                            <span className="text-accent">{typedText}</span>
                            <span className="cursor-blink"></span>
                            &quot;;
                        </p>
                    </motion.div>

                    {/* Description */}
                    <motion.p
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.6, delay: 0.6 }}
                        className="max-w-2xl mx-auto text-base sm:text-lg text-muted-foreground mb-10 leading-relaxed"
                    >
                        8+ years of experience in IT operations, digital transformation, and web technologies.
                        Specialized in cybersecurity, e-commerce solutions, and enterprise systems.
                    </motion.p>

                    {/* CTA Buttons */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.6, delay: 0.8 }}
                        className="flex flex-col sm:flex-row items-center justify-center gap-4 mb-12"
                    >
                        <Button
                            variant="hackerSolid"
                            size="lg"
                            onClick={() => document.querySelector('#projects')?.scrollIntoView({ behavior: 'smooth' })}
                            className="font-mono w-full sm:w-auto"
                        >
                            <FileText className="w-5 h-5" />
                            View Projects
                        </Button>
                        <Button
                            variant="hacker"
                            size="lg"
                            onClick={() => document.querySelector('#contact')?.scrollIntoView({ behavior: 'smooth' })}
                            className="font-mono w-full sm:w-auto"
                        >
                            <Mail className="w-5 h-5" />
                            Contact Me
                        </Button>
                    </motion.div>

                    {/* Social Links */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.6, delay: 1 }}
                        className="flex items-center justify-center gap-4"
                    >
                        {socialLinks.map((link, index) => (
                            <motion.a
                                key={link.label}
                                href={link.href}
                                target="_blank"
                                rel="noopener noreferrer"
                                initial={{ opacity: 0, scale: 0.8 }}
                                animate={{ opacity: 1, scale: 1 }}
                                transition={{ delay: 1 + index * 0.1 }}
                                whileHover={{ scale: 1.1, y: -2 }}
                                whileTap={{ scale: 0.95 }}
                                className="w-12 h-12 rounded-lg border border-primary/30 bg-card/50 flex items-center justify-center text-muted-foreground hover:text-primary hover:border-primary/60 hover:bg-primary/10 transition-all duration-300 hover:shadow-[0_0_20px_hsl(160_100%_50%/0.2)]"
                                aria-label={link.label}
                            >
                                <link.icon className="w-5 h-5" />
                            </motion.a>
                        ))}
                    </motion.div>
                </div>
            </div>

            {/* Scroll Indicator */}
            <motion.button
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 1.5 }}
                onClick={scrollToAbout}
                className="absolute bottom-8 left-1/2 -translate-x-1/2 flex flex-col items-center gap-2 text-muted-foreground hover:text-primary transition-colors duration-300 cursor-pointer"
            >
                <span className="font-mono text-xs">scroll_down</span>
                <ChevronDown className="w-6 h-6 scroll-indicator" />
            </motion.button>
        </section>
    );
};

export default HeroSection;
